import { StrictMode } from "react";
import { Link } from "react-router-dom";
import { Nav, NavItem } from "reactstrap";
import { Hr } from ".";
import { ScrollTopPag, Link as LinkScroll } from "../components";

export const Footer = () => {

    return (
        <StrictMode>
            <footer>
                <div className="footer-brand container-fluid">
                    <Link className="navbar-brand" to="/about">Diego Aldana</Link>
                </div>
                <div className="footer-url">
                    <p>Guia</p>
                    <Nav vertical>
                        <NavItem className="nav-title">
                            Gua
                        </NavItem>
                        <NavItem>
                            <Link onClick={ScrollTopPag} className="nav-link" to="/interes-simple">
                                Inters Simple
                            </Link>
                        </NavItem>
                        <NavItem>
                            <Link onClick={ScrollTopPag} className="nav-link" to="/interes-compuesto">
                                Inters Compuesto
                            </Link>
                        </NavItem>
                        <Hr/>
                        <NavItem>
                            <Link onClick={ScrollTopPag} className="nav-link" to="/descuento-simple">
                                Descuento Simple
                            </Link>
                        </NavItem>
                        <NavItem>
                            <Link onClick={ScrollTopPag} className="nav-link" to="/descuento-compuesto">
                                Descuento Compuesto
                            </Link>
                        </NavItem>
                    </Nav>
                </div>
            </footer>
        </StrictMode>
    );
};
